@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Satisfy");
/*
Theme Name: Pixietubeuse
Author: Jérémy Young & Sandrine Martinez
Description: Thème personnalisé pour le site Pixietubeuse
Version: 1.0
*/
html, body {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

body {
  line-height: 1; }

body:not(.wp-admin) {
  /* http://meyerweb.com/eric/tools/css/reset/
     v2.0 | 20110126
     License: none (public domain)
    */
  /* HTML5 display-role reset for older browsers */ }
  body:not(.wp-admin) div, body:not(.wp-admin) span, body:not(.wp-admin) applet, body:not(.wp-admin) object, body:not(.wp-admin) iframe,
  body:not(.wp-admin) h1, body:not(.wp-admin) h2, body:not(.wp-admin) h3, body:not(.wp-admin) h4, body:not(.wp-admin) h5, body:not(.wp-admin) h6, body:not(.wp-admin) p, body:not(.wp-admin) blockquote, body:not(.wp-admin) pre,
  body:not(.wp-admin) a, body:not(.wp-admin) abbr, body:not(.wp-admin) acronym, body:not(.wp-admin) address, body:not(.wp-admin) big, body:not(.wp-admin) cite, body:not(.wp-admin) code,
  body:not(.wp-admin) del, body:not(.wp-admin) dfn, body:not(.wp-admin) em, body:not(.wp-admin) img, body:not(.wp-admin) ins, body:not(.wp-admin) kbd, body:not(.wp-admin) q, body:not(.wp-admin) s, body:not(.wp-admin) samp,
  body:not(.wp-admin) small, body:not(.wp-admin) strike, body:not(.wp-admin) strong, body:not(.wp-admin) sub, body:not(.wp-admin) sup, body:not(.wp-admin) tt, body:not(.wp-admin) var,
  body:not(.wp-admin) b, body:not(.wp-admin) u, body:not(.wp-admin) i, body:not(.wp-admin) center,
  body:not(.wp-admin) dl, body:not(.wp-admin) dt, body:not(.wp-admin) dd, body:not(.wp-admin) ol, body:not(.wp-admin) ul, body:not(.wp-admin) li,
  body:not(.wp-admin) fieldset, body:not(.wp-admin) form, body:not(.wp-admin) label, body:not(.wp-admin) legend,
  body:not(.wp-admin) table, body:not(.wp-admin) caption, body:not(.wp-admin) tbody, body:not(.wp-admin) tfoot, body:not(.wp-admin) thead, body:not(.wp-admin) tr, body:not(.wp-admin) th, body:not(.wp-admin) td,
  body:not(.wp-admin) article, body:not(.wp-admin) aside, body:not(.wp-admin) canvas, body:not(.wp-admin) details, body:not(.wp-admin) embed,
  body:not(.wp-admin) figure, body:not(.wp-admin) figcaption, body:not(.wp-admin) footer, body:not(.wp-admin) header, body:not(.wp-admin) hgroup,
  body:not(.wp-admin) menu, body:not(.wp-admin) nav, body:not(.wp-admin) output, body:not(.wp-admin) ruby, body:not(.wp-admin) section, body:not(.wp-admin) summary,
  body:not(.wp-admin) time, body:not(.wp-admin) mark, body:not(.wp-admin) audio, body:not(.wp-admin) video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline; }
  body:not(.wp-admin) article, body:not(.wp-admin) aside, body:not(.wp-admin) details, body:not(.wp-admin) figcaption, body:not(.wp-admin) figure,
  body:not(.wp-admin) footer, body:not(.wp-admin) header, body:not(.wp-admin) hgroup, body:not(.wp-admin) menu, body:not(.wp-admin) nav, body:not(.wp-admin) section {
    display: block; }
  body:not(.wp-admin) ol, body:not(.wp-admin) ul {
    list-style: none; }
  body:not(.wp-admin) blockquote, body:not(.wp-admin) q {
    quotes: none; }
  body:not(.wp-admin) blockquote:before, body:not(.wp-admin) blockquote:after,
  body:not(.wp-admin) q:before, body:not(.wp-admin) q:after {
    content: '';
    content: none; }
  body:not(.wp-admin) table {
    border-collapse: collapse;
    border-spacing: 0; }

/* fonts */
/* breakpoints */
/* colors */
header {
  background-color: #024269; }

header .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    header .content {
      width: 1000px; } }

nav .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    nav .content {
      width: 1000px; } }

@media screen and (min-width: 1200px) {
  #pixie-sidebar {
    float: left;
    width: 250px; } }

@media screen and (min-width: 1200px) {
  .posts-list {
    float: left;
    width: 750px; } }

footer .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    footer .content {
      width: 1000px; } }

#pixie-front-page .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    #pixie-front-page .content {
      width: 1000px; } }
  #pixie-front-page .content::after {
    clear: both;
    content: "";
    display: block; }
